___CSS_LOADER_EXPORT___.push([module.id, `.product-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.product-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.product-image-section {
  width: 100%;
}

.product-image {
  /* Add these new styles */
  width: 100%;
  height: auto;
  border-radius: 8px;
  cursor: pointer;
  }
  
  .asset-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  cursor: pointer;
  }
  
  .asset-overlay img,
  .asset-overlay video {
  max-width: 90%;
  max-height: 90vh;
  object-fit: contain;
  }
  
  /* Update existing styles */
  .product-image-section video {
  width: 100%;
  height: auto;
  object-fit: cover;
  }

.product-details-section {
  padding: 1rem;
}

.product-name {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #111;
}

.product-price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #111;
  margin-bottom: 1.5rem;
}

.product-description {
  font-size: 1rem;
  line-height: 1.6;
  color: #484848;
  margin-bottom: 2rem;
}

.description-container {
  margin: 1.5rem 0;
}

.description-container p {
  margin-bottom: 1rem;
  line-height: 1.6;
  color: #484848;
}

.description-container p:last-child {
  margin-bottom: 0;
}

.add-to-cart-button {
  background-color: #0058a3;
  color: white;
  border: none;
  padding: 1rem 2rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.add-to-cart-button:hover {
  background-color: #004f93;
}

@media (max-width: 768px) {
  .product-container {
    grid-template-columns: 1fr;
  }
}

/* Swiper */
.product-image-section {
    width: 100%;
    max-width: 500px;
}

.product-image {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 8px;
}

.swiper {
  height: fit-content;
}

.swiper-slide {
  height: auto;
}

.product-asset,
.product-image {
  display: block;
  width: 100%;
  height: auto;
  max-height: 600px; /* Optional: set a maximum height */
  object-fit: contain;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
  
    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

.image-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  cursor: pointer;
}

.image-overlay img {
  max-width: 90%;
  max-height: 90vh;
  object-fit: contain;
  cursor: pointer;
}

.product-image {
  cursor: pointer;
  transition: opacity 0.2s;
}

.product-image:hover {
  opacity: 0.9;
}

.price-container {
  margin-bottom: 1.5rem;
}

.original-price {
  font-size: 1rem;
  color: #666;
  margin-top: 0.5rem;
}

.strikethrough {
  text-decoration: line-through;
  color: #999;
}

.product-metadata {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid #eee;
}

.original-product-link {
  display: inline-block;
  color: #0058a3;
  text-decoration: none;
  font-weight: 500;
  margin-top: 0.5rem;
  transition: color 0.2s;
}

.original-product-link:hover {
  color: #004f93;
  text-decoration: underline;
}
